export default {
  name: 'dateShow',
  data () {
    return {
      dayDiff: this.dayDiff,
      nowDayDiff: ''
    }
  },
  props: {
    project: Object
  },
  mounted () {
    console.log(this.project)
  },
  methods: {
    get (sTime, eTime) {
      sTime = new Date(sTime.replace(/-/g, '/'))
      eTime = new Date(eTime.replace(/-/g, '/'))
      let diff = eTime.getTime() - sTime.getTime()
      this.dayDiff = Math.floor(diff / (24 * 3600 * 1000))
      let date = new Date()
      let nowDiff = date.getTime() - sTime.getTime()
      this.nowDayDiff = Math.floor(nowDiff / (24 * 3600 * 1000))
      console.log(this.dayDiff)
      console.log(this.nowDayDiff)
      return this.dayDiff
    }
  },
  watch: {
    project: (newVal, oldVal) => {
      this.data = newVal
      let sTime = this.data.start_time
      console.log(sTime)
      let eTime = this.data.end_time
      sTime = new Date(sTime.replace(/-/g, '/'))
      eTime = new Date(eTime.replace(/-/g, '/'))
      let diff = eTime.getTime() - sTime.getTime()
      this.dayDiff = Math.floor(diff / (24 * 3600 * 1000))
      let date = new Date()
      let nowDiff = date.getTime() - sTime.getTime()
      this.nowDayDiff = Math.floor(nowDiff / (24 * 3600 * 1000))
      console.log(this.dayDiff)
      console.log(this.nowDayDiff)
      return this.dayDiff
    }
  }
}



