import ProjectHeader from './header'
import DateShow from './dateShow'
import Module from './module'
import axios from 'axios'

export default {
  name: 'projectPace',
  components: {
    ProjectHeader,
    DateShow,
    Module
  },
  data () {
    return {
      project: Object,
      name: ''
    }
  },
  methods: {
    getProject () {
      sessionStorage.setItem('projectId', this.$route.params.pId)
      axios.get('/api/project.json')
      // axios.get(this.url + '/project/id/' + this.$route.params.pId)
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      if (res) {
        const data = res.data
        this.project = data
        console.log(this.project)
        this.name = data.name
      }
    }
  },
  created () {
    this.getProject()
  }
}



